import React from "react"
import {  StaticQuery, graphql, Link } from "gatsby"
import * as scss from "./ReposByTag.module.scss"
import Tags from "../data/tags.json"

const getTagIndex = (tagName) => {
  const res = Tags.findIndex((t) => t.key === tagName)

  if (res === -1) return 999

  return res
}

const getTagByKey = (tagName) => {
  return Tags.find((t) => t.key === tagName) || false
}

const sortTags = (a, b) => {
  const idxA = getTagIndex(a)
  const idxB = getTagIndex(b)

  if (idxA > idxB) {
    return 1;
  }
  if (idxA < idxB) {
    return -1;
  }
  return 0;
}

const renderTagList = (tags) => {
  const items = []

  for (const t of tags.sort((a, b) => sortTags(a, b))) {
    let tagClass = "bx--tag"
    if (getTagByKey(t)) {
      tagClass += " bx--tag--green"
    } else {
      tagClass += " bx--tag--cool-gray"
    }
    items.push(<button class={tagClass}> <span class="bx--tag__label" title={getTagByKey(t).name}>{t}</span> </button>)
  }

  return items
}

const getPage = (d, url) => {
  const slug = url.replace("/toolkit/","")
  const pages = d.allMdx.edges

  const res = pages.filter( p => p.node.slug === slug)

  return res[0].node
}

const renderFunction = (d, url) => {
  const data = getPage(d, url)
  const fm = data.frontmatter
  const slug = data.slug
  const to = `/toolkit/${slug}`

  const item = (
    <div>
      <div className={scss.pb}>
        <h4>{fm.title}</h4>
        <p className={scss.italic}>{fm.description}</p>
      </div>
      <p className={scss.pt}>{renderTagList(fm.tags)}</p>
    </div>
  )

  return <Link to={to} className={scss.tile}>{item}</Link>
}

const RepoLink = (url) => (
  <StaticQuery
    query={graphql`
    {
      allMdx(
        filter: {fields: {source: {eq: "toolkit"}}, frontmatter: {tags: {}}, slug: {}}
        sort: {fields: frontmatter___weight, order: ASC}
      ) {
        edges {
          node {
            id
            slug
            frontmatter {
              description
              tags
              url
              title
            }
          }
        }
      }
    }
    `}
    render={data => renderFunction(data, url.to)}
  />
)


export default RepoLink



