import React from "react"
import { Link } from "gatsby"
import * as scss from "./RepoHeader.module.scss"
import Tags from "../data/tags.json"

const getTagIndex = (tagName) => {
  const res = Tags.findIndex((t) => t.key === tagName)

  if (res === -1) return 999

  return res
}

const getTagByKey = (tagName) => {
  return Tags.find((t) => t.key === tagName) || false
}

const sortTags = (a, b) => {
  const idxA = getTagIndex(a)
  const idxB = getTagIndex(b)

  if (idxA > idxB) {
    return 1;
  }
  if (idxA < idxB) {
    return -1;
  }
  return 0;
}

const RepoHeader = (frontmatter) => {
  const { url, tags } = frontmatter.frontmatter
  const items = []

  for (const t of tags.sort((a, b) => sortTags(a, b))) {
    let tagClass = "bx--tag"
    if (getTagByKey(t)) {
      tagClass += " bx--tag--green"
    } else {
      tagClass += " bx--tag--cool-gray"
    }

    items.push(<Link to={`/toolkit#${t}`} key={t}><button class={tagClass}> <span class="bx--tag__label" title={getTagByKey(t).name}>{t}</span> </button></Link>)
  }

  return (
    <div className="bx--grid">
      <div className="bx--row">
        <div className="bx--col-lg-1">Repository: </div>
        <div className="bx--col-lg-4"><a href={url} target="_blank" rel="noreferrer">{url}</a></div>
      </div>
      <div className="bx--row">
        <div className="bx--col-lg-1 category-header">Categories:</div>
        <div className="bx--col-lg-4"><div className={scss.flex_sm}>{items}</div></div>
      </div>
    </div>
  )
}

export default RepoHeader



